
body {
  margin: 0;
  overflow: hidden;	
}
canvas { 
  display: block; 
}

#c {
  width: 100%;
  height: 100%;
  display: block;
  background: #000046;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #1CB5E0, #000046);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #1CB5E0, #000046); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-size: cover;
}


.wreath-border.left{
	position: absolute;
	z-index: 15;
	pointer-events: none;
	top: -50px;
	left: -50px;
	max-width: 80%;
	user-select: none;
}

.wreath-border.right{
	position: absolute;
	z-index: 15;
	pointer-events: none;
	top: -50px;
	right: -50px;
	transform: scaleX(-1);
	max-width: 80%;
	user-select: none;
}


.wreath-border>img {
	height: auto;
    width: 500px; 
	max-width: 100%;
}

.modal-content {
	border: 15px black solid;
	box-shadow: 0 0 3pt 2pt white;
	border-radius: 20px;
	top: 20vh;
}

.modal {
	/* backdrop-filter: blur(6px); */
}

.modal-backdrop.show {
    opacity: 0.7;
}

.modal-content>img {
	width: 100%;
	position: absolute;
	bottom: 100%;
}

.modal-dialog {
	width: 90vw;
	max-width: 900px;
}

.settings-button {
	position: absolute;
	top: 80%;
	left: 5%;
	width: 20vw;
	max-width: 150px;
	min-width: 20px;
	user-select: none;
}


#test_slider {
	position: absolute;
	top: 55%;
	left: 3%;
}	